import { Component, Vue, Mixins } from 'vue-property-decorator';
import { ComponentOptions } from 'vue';
import SubDemo from './subDemo.vue';
import DemoMixin from '@/mixins/demoMixin';
function clog(preStr: any) {
  return function innerlog(str: any) {
    console.log(preStr, str);
  };
}
const lifeLog = clog('生命周期:');

@Component({
  components: {
    SubDemo,
  },
})
export default class Demo extends Mixins(DemoMixin) {
  private msg: string = '这里是data数据';
  private num: number = 0;
  private text: string = '';
  // computed
  get subDemoIsShow() {
    return this.num < 3;
  }
  set subDemoIsShow(val) {
    this.num = 5;
  }

  public beforeCreate() {
    lifeLog('beforeCreate');
  }
  public created() {
    lifeLog('created');
  }
  public beforeMount() {
    lifeLog('beforeMount');
  }
  public mounted() {
    lifeLog('mounted');
  }
  public beforeUpdate() {
    lifeLog('beforeUpdate');
  }
  public updated() {
    lifeLog('updated');
  }
  public activated() {
    lifeLog('activated');
  }
  public deactivated() {
    lifeLog('deactivated');
  }
  public beforeDestroy() {
    lifeLog('beforeDestroy');
  }
  public destroyed() {
    lifeLog('destroyed');
  }
  public errorCaptured() {
    lifeLog('errorCaptured');
  }
  public changeTTT() {
    console.log('demo index.vue changeTTT');
    this.ttt = 'ttt3';
  }
  private changeMsg() {
    this.msg = `${this.msg}${this.num++}`;
    this.ttt = '====tttt';
  }
}

